body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.root {
  margin: 5%;
}

.root .panel {
  margin-right: -15px;
  margin-left: -15px;
}

.root .panel .panel_header {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  font-size: 14px;
  position: relative;
  z-index: 100;
  background-image: linear-gradient(
    90deg,
    #fc0 0,
    #fc0 30%,
    #ffde59 79%,
    #fff0b2 100%
  );
}

.header_logo {
  width: 11rem;
  margin-top: 1rem;
  vertical-align: middle;
}

.panel_header_menu {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: red;
}

.panel_header_menu .menu {
  margin: 0 10px;
}

.caurosel .sky {
  width: 100%;
  height: 335px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url("https://www.dhl.de/en/geschaeftskunden/express/verfolgen/_jcr_content/stage/parsys/responsivepar/stageitem/image/img.sly.1478701739436.1200.medium.crop.7x2.png");
}

.text {
  width: 60%;
}
.text p span {
  font-size: 2.2rem;
  line-height: 2.5rem;
  color: #d40511;
}

.text h1 span {
  margin-top: 0.5rem;
  font-size: 2.1rem;
  display: block;
  font-family: inherit;
  font-weight: 800;
  color: #d40511;
}

.tracking_container {
  padding: 0 15px;
  font-size: 14px;
  position: relative;
  z-index: 100;
  background-image: linear-gradient(
    90deg,
    #fc0 0,
    #fc0 30%,
    #ffde59 79%,
    #fff0b2 100%
  );
}

.tracking_input {
  display: flex;
  align-items: center;
  justify-content: center;
}

.tracking_input h5 {
  margin: 0px 20px;
}

.tracking_input input {
  width: 300px;
  border: none;
  outline: none;
  padding: 8px 27px;
  margin: 12px 20px;
}

.tracking_input button {
  margin-left: -20px;
  width: 40px;
  height: 37px;
  border: 2px solid #d40511;
  outline: none;
  background: #d40511;
  color: #fff;
  border-radius: 2px;
}

.all-caps {
  text-transform: uppercase;
  font-weight: 800;
}

.tracking_information {
  display: flex;
  align-items: center;
  justify-content: center;
}

.tracking_information .shipment_details {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
  padding-top: 20px;
  animation-duration: 1s;
  animation-name: slideInUp;
  margin-top: 10px;
  visibility: visible;
  max-width: 610px;
  padding: 9px 20px 16px;
  animation-fill-mode: both;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 0 5px rgb(0 0 0 / 20%);
  margin-top: 10px;
  padding: 9px 10px 12px;
  width: 100%;
  will-change: margin-top;
}

.shipment_number span {
  font-size: 0.9rem;
}

.current_status {
  margin-top: 40px;
}

.location {
  font-size: 0.9rem;
  font-weight: 800;
}

@media screen and (min-width:320px) {
  .caurosel .sky {
    height: 150px;
  }

  .caurosel .sky .text h1 span {
    display: none;
  }
}
